  import React, { PureComponent, Suspense } from "react";
  import "../assets/css/analytics.css";
  import { Col, Row } from "react-bootstrap";
  import { connect } from "react-redux";
  import { getAllContentData } from "../../../../actions/analyticsNew";

  const TopCard = React.lazy(() => import("../layout/common/topCard"));
  const SentimentPost = React.lazy(() =>
    import("./components/sentimentPost")
  );
  const SentimentAnalytics = React.lazy(() =>
    import("./components/sentimentAnalytics")
  );
  const LeaderBoard = React.lazy(() =>
    import("./components/leaderBoard")
  );
  const TopeventVoice = React.lazy(() =>
    import("./components/topeventVoice")
  );
  const PosttypeCount = React.lazy(() =>
    import("./components/posttypeCount")
  );
  const TrueSense = React.lazy(() => import("./components/trueSense"));
  const AssetsLanguage = React.lazy(() =>
    import("./components/assetsLanguage")
  );
  const AssetsNetwork = React.lazy(() =>
    import("./components/assetsNetwork")
  );
  const Tagcloud = React.lazy(() => import("./components/tagCloud"));

  const ContentAnalytics = React.lazy(() =>
    import("./components/aontentAnalytics")
  );

  const TopAiTags = React.lazy(() =>
    import("./components/topAiTags")
  );

  class Content extends PureComponent {

    componentDidMount() {
      const { getAllContentData, analyticsRecords, } = this.props;  
        if (!analyticsRecords.contentCount.length)  getAllContentData()
    }

    render() {

      const { analyticsRecords, selectedWallFeed, authenticated, analyticsStatus } = this.props

      const {
        contentAnalytics,
        truSense,
        sentimentPost,
        getFlags,
        leaderBoard,
        topeventVoices,
        wordCloud,
        topaiTags,
        postbyLanguage,
        postbyNetwork,
        postTypeCount,
        sentiment_loader,
        content_loader,
        leader_board_loader,
        top_event_loader,
        word_loader,
        trusense_loader,
        top_ai_loader,
        post_by_language_loader,
        post_type_count_loader, 
        posts_by_network_loader,
        get_wall_sentiment_live_trend_data,
        get_wall_sentiment_live_trend_loader,
        dateFilter,
        selected_feed_data
      } = analyticsRecords;

       const  selectedWallFeedData = { selectedWallFeed : selectedWallFeed }
       const isUpgrade = authenticated.userData.user.trueSense
       const isTrial = !selected_feed_data ||  analyticsStatus

      return (
        <>
              <Row>
                <Col lg={8}>
                  <Suspense fallback={<></>}>
                  <div className="row">
                    <TopCard col={3} res={4} loader={content_loader} data={contentAnalytics} /> 
                  </div>  
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <ContentAnalytics key={content_loader} loader={content_loader} contentAnalytics={contentAnalytics} />
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <SentimentAnalytics {...selectedWallFeedData} isUpgrade={isUpgrade}  loader={get_wall_sentiment_live_trend_loader} dateFilter={dateFilter} live_trend={get_wall_sentiment_live_trend_data} 
                     sentimentPost={sentimentPost}  />
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <SentimentPost isTrial={isTrial} loader={sentiment_loader} {...sentimentPost} {...selectedWallFeedData} />
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <LeaderBoard isTrial={isTrial}  loader={leader_board_loader} {...selectedWallFeedData} leaderBoard={leaderBoard} />
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <TopeventVoice isTrial={isTrial} top_event_loader={top_event_loader} {...selectedWallFeedData} topeventVoices={topeventVoices} />
                  </Suspense>
                </Col>

                <Col lg={4}>
                  <Suspense fallback={<></>}>
                    <TrueSense loader={trusense_loader} truSense={truSense} {...selectedWallFeedData} dateFilter={dateFilter} />
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <Tagcloud key={word_loader} word_loader={word_loader} wordCloud={wordCloud} />
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <TopAiTags key={top_ai_loader} top_ai_loader={top_ai_loader}  {...selectedWallFeedData}  topaiTags={topaiTags} />
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <PosttypeCount key={post_type_count_loader} loader={post_type_count_loader} {...selectedWallFeedData} dateFilter={dateFilter}  postTypeCount={postTypeCount} />
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <AssetsLanguage key={post_by_language_loader} loader={post_by_language_loader}  getFlags={getFlags} detail={this.props.detail} {...selectedWallFeedData} postbyLanguage={postbyLanguage} />
                  </Suspense>

                  <Suspense fallback={<></>}>
                    <AssetsNetwork loader={posts_by_network_loader}  postbyNetwork={postbyNetwork} />
                  </Suspense>
                </Col>
              </Row>
         </>
      );
    }
  }

  const contentStateToProps = (state) => { 
    return {
      analyticsRecords: state.analyticsRecords,
      detail: state.appsMore.detail,
      selectedWallFeed: state.analyticwall.selectedWallFeed.feeds,
      authenticated : state.authenticated,
      analyticsStatus: state.analyticsStatus.trialAccessAPI,
    };
  };


  const contentDispatchToProps = (dispatch) => {
    return {
      getAllContentData: () => dispatch(getAllContentData()),
    };
  };

  export default connect(contentStateToProps, contentDispatchToProps)(Content);
